import { Injectable } from '@angular/core';

@Injectable()
export class ParmService {

  constructor() { }
  obj = {
      id:"1",
      name:"open",
      rollno:"2233"
  }


    ngBuildMode = true;  // set for dev mode
    imgPath = "../../assets/images/";
 
    getImgPath(){
        // export mode
        // ng build --prod
        if (this.ngBuildMode){ 
            this.imgPath = "./assets/images/"; 
        }

        return this.imgPath;
    }


    success(){return "Successful" ;}
  
}

 



